* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: linear-gradient(to top right, rgba(11, 11, 11, .82) 7%, rgba(224, 230, 39, .655) 52%, rgba(12, 144, 171, .706) 87%) 0 0 / 100vw 100vh no-repeat fixed;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

#search-box {
  width: 37vw;
  background: linear-gradient(to top right, rgba(11, 11, 11, .153) 7%, rgba(224, 230, 39, .655) 52%, rgba(12, 144, 171, .706) 87%);
  border: none;
  border-radius: 4px;
  margin-bottom: 16px;
  padding: 8px 20px;
  transition: scale, box-shadow .3s cubic-bezier(1, .37, .71, .79);
  position: relative;
  top: 0;
  left: 2vw;
  box-shadow: 0 0 6px 2px rgba(0, 0, 0, .75);
}

#search-box:focus {
  color: #000;
  outline: #cd5c5c;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 21px;
  font-weight: 500;
  transform: scale(1.1);
  box-shadow: 0 0 6px 2px rgba(255, 255, 255, .75);
}

.country-item {
  text-shadow: 1px 1px 2px rgba(255, 255, 255, .8);
  flex-wrap: nowrap;
  font-size: 25px;
  font-weight: 700;
  display: flex;
}

.country-item + .country-item {
  margin-top: 10px;
}

.country-icon {
  border-radius: 4px;
  margin-right: 10px;
  display: block;
  box-shadow: 0 0 6px 2px rgba(255, 255, 255, .75);
}

.country-title {
  text-shadow: 1px 1px 2px rgba(255, 255, 255, .8);
  font-size: 45px;
}

.country-description-item {
  text-shadow: 1px 1px 2px rgba(255, 255, 255, .8);
  font-size: 18px;
  font-weight: 700;
}

